.Sert {
  position: relative;
  text-align: center;
  height: 100vh;
  overflow: hidden;
}

.Sert-Title {
  z-index: -100;
}

.Sert-Bg {
  background-color: lightpink;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  -webkit-clip-path: polygon(17% 0%, 0 66%, 50% 100%, 0 13%);
  clip-path: polygon(17% 0%, 0 66%, 50% 100%, 0 13%);
}

.Sert-Wrapper {
  display: flex;
  overflow: visible;
  margin-top: 40px;
  padding: 0px 20px;
  height: 100%;
}

.Sert-Left {
  flex: 1;
  position: relative;
  overflow: visible;
}

.Sert-Img {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  width: 200px;
  height: 300px;
  border-radius: 10px;
  overflow: visible;
}

.Sert-ImgOne {
  background-image: url('../../img/sertificate/sert.jpg');
  position: absolute;
  top: 0;
  left: 20px;
}

.Sert-ImgTwo {
  background-image: url('../../img/sertificate/sert2.jpg');
  position: absolute;
  top: 50px;
  left: 200px;
}

.Sert-ImgThree {
  background-image: url('../../img/sertificate/sert3.jpg');
  position: absolute;
  top: 100px;
  left: 370px;
}

.Sert-Right {
  flex: 1;
  text-align: left;
  ul {
    margin-top: 30px;

    flex-direction: column;
    li {
      margin-bottom: 50px;
    }
  }
}

@media (max-width: 1200px) {
  .Sert {
    overflow: visible;
  }
  .Sert-Bg {
    background-color: lightpink;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    -webkit-clip-path: polygon(17% 0%, 0 59%, 100% 26%, 0 13%);
    clip-path: polygon(17% 0%, 0 59%, 100% 26%, 0 13%);
  }
  .Sert-Wrapper {
    flex-direction: column;
  }
  .Sert-Left {
    flex: 1;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    .Sert-Img {
      position: static;
      margin: 0px 5px 20px;
    }
  }
  .Sert-Right {
    flex: 1;
    overflow: visible;
    padding-bottom: 30px;
    ul {
      li {
        margin-bottom: 15px;
      }
    }
  }
}
